import React from 'react';
//<i className="fa fa-refresh fa-spin fa-3x fa-fw" /> #d5f4e6
//<h3><Badge color="primary">SHANDILYA</Badge></h3>
// L25 div className="container main-content"
import { Row, Col } from 'reactstrap';

class Punchline extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isToggleOn: true };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(props) {
    this.setState(prevState => ({
      isToggleOn: !prevState.isToggleOn
    }));
  }

  render(){
  	let punchMessage = null;
  	if (!this.props.underline) punchMessage = <p className="heading-content">{this.props.tag}</p>
  	else punchMessage = <p className="heading-content">&nbsp;{this.props.tag}&nbsp;</p>
  	return(
	  	<div className="container">
	        <Row>
	            <Col xs="12" sm="12" lg="12" >
	            	{punchMessage}
	            </Col>
	        </Row>
	    </div>
  	);
  }
}

export default Punchline;



